html,
body {
  /* height: 100%; */
  /* width: 100%; */
  padding: 0;
  margin: 0;
  background: var(--navy);
  font-weight: 400;
  color: #444;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
  outline: none;
}
* {
  box-sizing: border-box;
}

#app {
  height: 100%;
}

:root {
  --dark: #000007;
  --darkestNavy: #01050b;
  --darkNavy: #020c1b;
  --navy: #0a192f;
  --lightNavy: #172a45;
  --darkGrey: #333f58;
  --mediumGrey: #2d3952;
  --grey: #4c5772;
  --lightGrey: #606a86;
  --slate: #8892b0;
  --lightSlate: #a8b2d1;
  --lightestSlate: #ccd6f6;
  --offWhite: #dce7ff;
  --white: #e6f1ff;
  --pink: #ff647f;
  --yellow: #ffc464;
  --orange: #ff9e64;
  --green: #64ffda;
  --blue: #71afff;
  --darkBlue: #1d7ffc;
  --purple: #ae80ff;
  --highlight: rgba(41; 61; 90; 0.99);
  --transGreen: rgba(100; 255; 218; 0.07);
  --transNavy: rgba(10; 25; 47; 0.7);
  --shadowNavy: rgba(2; 12; 27; 0.7);

  --fontSizesXS: 12px;
  --fontSizesSmish: 13px;
  --fontSizesSM: 14px;
  --fontSizesMD: 16px;
  --fontSizesLG: 18px;
  --fontSizesXL: 20px;
  --fontSizesXXL: 22px;
  --fontSizesH3: 32px;

  --tablet: 48em;
  --phone: 30em;
  --desktop: 62.5em;
}

.fadeup-enter {
  opacity: 0.01;
  transform: translateY(20px);
  transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
    transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
}
.fadeup-enter-active {
  opacity: 1;
  transform: translateY(0px);
  transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
    transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
}
.fadedown-enter {
  opacity: 0.01;
  transform: translateY(-20px);
  transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
    transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
}
.fadedown-enter-active {
  opacity: 1;
  transform: translateY(0px);
  transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
    transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
}
.fade-enter {
  opacity: 0.01;
  transition: opacity 1000ms cubic-bezier(0.645, 0.045, 0.355, 1);
}
.fade-enter-active {
  opacity: 1;
  transition: opacity 1000ms cubic-bezier(0.645, 0.045, 0.355, 1);
}



